@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body{margin:0;font-family:"Roboto Mono", monospace;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body input{font-family:"Montserrat", sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}button{color:#494949;font-family:"Montserrat", sans-serif;text-transform:uppercase;text-decoration:none;background:#ffffff;padding:5px;border:2px solid #494949;display:inline-block;transition:all 0.4s ease 0s;border-radius:2px;cursor:pointer}button:hover{color:#ffffff;background:black;border-color:black;transition:all 0.4s ease 0s}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.display-none{display:none}.display-block{display:block}.display-flex{display:flex}.cursor-pointer{cursor:pointer}.modal-main{position:fixed;width:80%;height:80%;top:50%;left:50%;transform:translate(-50%, -50%);display:flex;justify-content:center;align-items:center;border-radius:7px}

form{width:100%;height:100%;background:white;border:1px solid #ccc;padding:20px;box-shadow:2px 2px 5px rgba(0,0,0,0.3);border-radius:7px}form>button{margin-left:45%;margin-top:5px}form>div{display:flex;flex-flow:row nowrap;line-height:2em;margin:5px}form>div>label{color:#333;width:110px;font-size:1em;line-height:32px}form>div>input,form>div textarea{flex:1 1;padding:3px 5px;font-size:1em;margin-left:15px;border:1px solid #ccc;border-radius:3px5}form>div>div{margin-left:16px}form>div>div>label{display:block}form>div>div>label>input,form>div>div>label textarea{margin-right:3px}.modal-appear{opacity:0}.modal-enter-done{opacity:1;transition:opacity 0.3s}.modal-exit{opacity:1}.modal-exit-active{opacity:0;transform:scale(0.9);transition:opacity 300ms, transform 300ms}

.create-box{height:20% !important;display:flex;justify-content:center;align-items:center}.grid-section{height:80%}.overlay{position:absolute;top:0;left:0;right:0;bottom:0;background-color:rgba(0,0,0,0.85);background:url(data:;base64,iVBORw0KGgoAAAANSUhEUgAAAAIAAAACCAYAAABytg0kAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAgY0hSTQAAeiYAAICEAAD6AAAAgOgAAHUwAADqYAAAOpgAABdwnLpRPAAAABl0RVh0U29mdHdhcmUAUGFpbnQuTkVUIHYzLjUuNUmK/OAAAAATSURBVBhXY2RgYNgHxGAAYuwDAA78AjwwRoQYAAAAAElFTkSuQmCC) repeat scroll transparent\9;z-index:9999;color:white}

.note-card{display:flex;flex-direction:column;justify-content:space-between;height:150px;width:130px;background:white;border:1px solid #ccc;padding:10px;border-radius:7px;word-break:break-all;overflow:hidden;margin:10px;position:relative}.note-card:hover{box-shadow:2px 2px 5px rgba(0,0,0,0.3);transition:box-shadow 0.2s}.note-card:hover .actions{opacity:1}.note-card .actions{opacity:0;transition:opacity 0.5s ease;display:flex;justify-content:space-between}

.notes-grid{display:flex;flex-wrap:wrap}.note-card-appear{opacity:0}.note-card-enter-done{opacity:1;transition:opacity 0.3s}.note-card-exit{opacity:1}.note-card-exit-active{opacity:0;transform:scale(0.9);transition:opacity 300ms, transform 300ms}

.pinned-notes{margin:20px 0px;background-color:#f9f9f9;border-radius:7px;padding:20px}

.header{height:50px;background-color:#494949;display:flex;align-items:center;justify-content:space-between}.toggle-sidebar{margin:10px;padding:15px;cursor:pointer}.toggle-sidebar:hover{background-color:#f1f3f4;border-radius:2px}.search-bar{width:40%;height:25px;border:0px solid;border-radius:4px;padding:5px;box-shadow:none}

.display-none{display:none}.display-block{display:block}.display-flex{display:flex}.cursor-pointer{cursor:pointer}.sidebar{height:100vh;margin-right:20px;border-radius:5px}.sidebar-appear{opacity:0}.sidebar-enter-done{opacity:1;transition:opacity 0.3s}.sidebar-exit{opacity:1}.sidebar-exit-active{opacity:0;transform:scale(0.9);transition:opacity 300ms, transform 300ms}.items{padding:15px;border-radius:0px 15px 15px 0px}.items:hover{background-color:#f1f3f4;cursor:pointer}

.container{display:flex}.main{padding:30px;width:100%}

